/* ヘッダー */
.header-container {
  width: 84%;
  margin: 0 auto;

  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 74px;

  /* ヘッダー：サイト名 */
  &__site {
    display: flex;
    align-items: center;

    img {
      width: 42px;
      margin-right: 10px;
    }
  }

  //   &__text-wrapper {
  //   }

  &__corp {
    color: #333333;
    font-size: 12px;
    font-weight: 900;
    line-height: 1.4;
  }

  &__desc {
    color: #707f89;
    font-size: 10px;
    line-height: 1.4;
  }

  /* ナビゲーションボタン */
  &__nav-button {
    box-sizing: content-box;
    padding: 0;
    outline: none;
    border: none;
    background: none;
    width: 20px;
    height: 14px;
    cursor: pointer;
    color: #2e5180;
  }

  &__nav-button::before,
  &__nav-button::after {
    content: "";
    display: block;
    height: 2px;
    background-color: currentColor;
    transform: translateY(5px);
    transition: 0.3s ease-in-out;
  }

  &__nav-button::before {
    transform: translateY(-5px);
    box-shadow: 0 6px currentColor;
  }

  &__sr-only {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
}

@media (min-width: 900px) {
  .header-container {
    width: 91%;
    max-width: 1240px;
    height: 100px;

    &__site {
      img {
        width: 59px;
        margin-right: 18px;
      }
    }
    &__corp {
      font-size: 20px;
    }

    &__desc {
      font-size: 12px;
    }
  }
}
