/* ヒーロー */
.hero {
  height: 400px;
  background-image: url(../img/hero.jpg);
  background-position: center;
  background-size: cover;
  display: flex;
}

.hero .catch {
  flex: auto;
  align-self: flex-end;
  padding: 33px 0;
  background-color: rgba(255, 255, 255, 0.76);
}

.hero .container {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  line-height: 1.3;
}

.hero img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

.hero p {
  font-size: 14px;
  color: #2e5180;
  margin-bottom: 15px;
}

@media (min-width: 900px) {
  .hero {
    height: 700px;
  }

  .hero .catch {
    padding: 47px 0 44px;
  }

  .hero p {
    font-size: 16px;
    margin-bottom: 22px;
  }
}

/* ヒーロー：IE対応 */
@media (-ms-high-contrast: none), not all and (-ms-high-contrast: none) {
  .hero img {
    width: calc((100vw - 17px) * 0.84);
    max-width: 637.78px;
  }
}
